import React, {Component} from "react";

const imgPath = `${process.env.PUBLIC_URL}` + '/images/project/';


class Item extends Component {

  constructor(props) {
      super(props);
  }
  static defaultProps = {
    info: {
      id: 0,
      thumbnail: "",
      name: "",
      date : "",
      role : "",
      technic : ""
    }
  }

  render() {
    const {
      thumbnail, name, date, id , role, technic
    } = this.props.info;
    return (
      <div className={`chip chip-${id}`}>
        <div className={"item"}>
          <div className={"thumbnail"}>
            <span className={""}>
              <img src={`${imgPath}atomy.PNG'` }/>
              <img src={`./resources/images/project/${thumbnail}.PNG`} onError={e => {
                    // e.target.src = `${PUBLIC_URL}/images/thumbnail_no_image_s.png`;
                }}/>
            </span>
          </div>
          <div className={"data"}>
            <ul>
              <li>
                <strong>프로젝트명</strong>
                <span className={"name"}>{name}</span>
              </li>
              <li>
                <strong>일정</strong>
                <span className={"date"}>{date}</span>
              </li>
              <li>
                <strong>역할</strong>
                <span className={"role"}>{role}</span>
              </li>
              <li>
                <strong>주사용 기술</strong>
                <span className={"technic"}>{technic}</span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    );
  }
}

export default Item;
